   /* src/App.css */

/* General Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #525151;
  }
  
  h1, h2, h3 {
    color: #f1f103;
  }
  
  .App {
    text-align: center;
  }
  
  nav {
    background-color: #333;
    color: #eade07;
    padding: 10px;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    margin: 0;
  }
  
  nav ul li {
    margin: 0 10px;
  }
  
  nav ul li a {
    color: #e6ee0c;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 3px;
  }
  
  nav ul li a:hover {
    background-color: #555;
  }
  
  .container {
    width: 80%;
    margin: 20px auto;
  }
  
  .card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .card {
    background-color: #363434;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin: 10px;
    padding: 20px;
    width: 300px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .card h3 {
    margin-top: 0;
    color: #f1f103;
  }
  
  .card p {
    margin: 10px 0;
    color: #f1f103;
  }
  
  .skills, .about {
    padding: 20px;
    color: #f1f103;
  }
  
  .technical-skills, .soft-skills, .experience, .education, .blogs {
    margin-bottom: 20px;
    color: #f1f103;
  }
  
  .technical-skills ul, .soft-skills ul {
    list-style-type: none;
    padding: 0;
    color: #f1f103;
  }
  
  .technical-skills li, .soft-skills li {
    background: #535353;
    margin: 5px 0;
    padding: 10px;
    border-radius: 4px;
  }
  