/* src/components/EducationCard.css */

.card {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin: 20px auto;
    padding: 20px;
    max-width: 500px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    text-align: left;
  }
  
  .card h3 {
    color: #333;
    margin-top: 0;
  }
  
  .card p {
    color: #555;
    line-height: 1.6;
    margin: 5px 0;
  }
  
  .card a {
    display: inline-block;
    margin-top: 10px;
    color: #007BFF;
    text-decoration: none;
  }
  
  .card a:hover {
    text-decoration: underline;
  }
  